<template>
  <div class="PostList">
    <PostInput v-on:update="update()" />
    <section v-if="posts.length">
      <PostBlock
        v-for="post in posts.slice().reverse()"
        :key="post._id"
        :post="post"
        v-on:update="update()"
      ></PostBlock>
    </section>
    <section v-else-if="error">
      <span> Error : {{ errorText }}</span>
    </section>
    <section v-else-if="loading">
      <span>{{ loadingText }}</span>
    </section>
    <section v-else>
      <span>It's nothing here. Type to post the first post.</span>
    </section>
  </div>
</template>

<script>
import PostBlock from "./PostBlock.vue";
//import axios from "axios";
import PostService from "../PostService";
import PostInput from "./PostInput";

export default {
  name: "PostList",
  components: {
    PostBlock,
    PostInput,
  },
  data() {
    return {
      loadingText: "Loading the posts...",
      loading: 1,
      error: 0,
      errorText: "",
      posts: [],
    };
  },
  methods: {
    async update() {
      try {
        let posts = await PostService.getPost();
        this.posts = posts;
      } catch (err) {
        this.errorText = err;
        this.error = 1;
      }
    },
  },
  async created() {
    await this.update();
    setInterval(this.update.bind(this), 3000);
    this.loading = 0;
  },
};
</script>

<style scoped>
* {
  width: 100%;
  box-sizing: border-box;
  margin: 0;
}
.PostList {
  display: flex;
  flex-direction: column;
  align-items: center;
  margin: 0 auto;
  width: 100%;
}
</style>
