<template>
  <div class="PostInput">
    <input
      type="text"
      v-on:keydown.enter="addPost()"
      placeholder="Type Your Name"
      v-model="username"
    />
    <input
      type="text"
      v-on:keydown.enter="addPost()"
      placeholder="Say Something"
      v-model="article"
    />
  </div>
</template>

<script>
import PostService from "../PostService";
export default {
  name: "PostInput",
  data() {
    return {
      username: "",
      article: "",
    };
  },
  methods: {
    async addPost() {
      if (!this.username || !this.article) return;
      let post = { username: this.username, article: this.article };
      this.username = "";
      this.article = "";
      await PostService.addPost(post);
      this.$emit("update");
      return;
    },
  },
};
</script>

<style scoped>
* {
  box-sizing: border-box;
}
.PostInput {
  height: 4rem;
  display: flex;
  flex-direction: column;
  align-items: flex-start;
  justify-content: space-between;
  width: 100%;
  margin: 0.5rem;
  font-size: 1rem;
}
input {
  height: 45%;
  width: 100%;
  font-size: 100%;
  border-radius: 0.2rem;
  border: 0.05rem solid lightgrey;
  outline: none;
  padding: 0.3rem;
}
</style>
