<template>
  <div class="PostBlock">
    <div class="PostHead">
      <span>{{ post.username }}</span>
      <div>
        <span>{{ post.date }}</span>
        <div class="cross" v-on:click="delPost()"></div>
      </div>
    </div>
    <hr />
    <p>{{ post.article }}</p>
  </div>
</template>

<script>
import PostService from "../PostService";
export default {
  name: "PostBlock",
  props: {
    post: {
      type: Object,
      required: true,
    },
  },
  methods: {
    async delPost() {
      await PostService.delPost(this.post._id);
      this.$emit("update");
      return;
    },
  },
};
</script>

