import axios from "axios";

const url = "";
class PostService {
  static async getPost() {
    try {
      let result = await axios.get(url + "api/posts/");
      return result.data;
    } catch (err) {
      throw err.response.data;
    }
  }
  static async addPost(post) {
    try {
      await axios.post(url + "api/posts/", post);
      return;
    } catch (err) {
      throw err.response.data;
    }
  }
  static async delPost(postid) {
    try {
      await axios.delete(url + "api/posts/" + postid);
      return;
    } catch (err) {
      throw err.response.data;
    }
  }
}

export default PostService;
